body {
  font-family: Lato;
  font-size: 16px;
  word-wrap: break-word; }

.site-content {
  line-height: 1.5; }

.site-content {
  padding-top: 40px; }

.site-header {
  background-color: #f3f3f3;
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0px 1.5rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%; }
  .site-header a {
    text-decoration: none;
    color: inherit; }
  .site-header__brandingContainer {
    display: block;
    margin-right: auto; }
  .site-header__brandingImage {
    display: block;
    width: 30px;
    height: 30px; }
  .site-header__hamburgerContainer {
    display: block;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 50%;
    transition: background-color 300ms ease; }
  input:checked ~ .site-header__hamburgerContainer {
    background-color: #dadada; }
  .site-header__hamburgerImage {
    display: block;
    width: 18px;
    height: 18px; }
  .site-header__menu {
    list-style: none;
    margin: 0px;
    padding: 0px; }
  .site-header__category {
    transform: translate3d(0px, 0px, 0px); }
  .site-header__item {
    backface-visibility: hidden; }
  .site-header__input {
    display: none; }
  .site-header__links {
    list-style: none;
    padding: 0px; }
  .site-header__linkContainer {
    line-height: 1.1;
    padding: 0.25rem 1rem;
    transition: background-color 300ms ease; }

@media screen and (max-width: 700px) {
  .site-header__menu {
    z-index: 2;
    position: absolute;
    right: 0px;
    top: 0px;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    padding: 1rem 2rem 5rem;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    transition: all 300ms ease;
    transform: translate3d(100%, 0px, 0px);
    opacity: 0; }
  input:checked ~ .site-header__menu {
    transform: translate3d(0px, 0px, 0px);
    opacity: 1; }
  .site-header__item {
    margin-bottom: 1rem; }
    .site-header__item:last-child {
      margin-bottom: 0px; }
  .site-header__category {
    margin-bottom: 0.5rem;
    font-weight: 700;
    margin: 0px;
    cursor: pointer; }
  .site-header__links {
    padding-left: 0rem; }
  .site-header__hamburgerShadow {
    position: absolute;
    visibility: hidden;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    opacity: 0;
    transition: opacity 300ms ease; }
  input:checked ~ .site-header__hamburgerShadow {
    z-index: 1;
    visibility: visible;
    opacity: 1; } }

@media screen and (min-width: 701px) {
  .site-header__hamburgerContainer {
    display: none; }
  .site-header__hamburgerTrigger {
    display: none; }
  .site-header__hamburgerShadow {
    display: none; }
  .site-header__menu {
    display: flex; }
  .site-header__item {
    position: relative; }
    .site-header__item:before {
      content: "";
      z-index: 1;
      display: block;
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      background-color: #f3f3f3; }
  .site-header__category {
    z-index: 2;
    position: relative;
    padding: 0px 1.5rem;
    height: 40px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: background-color 300ms ease;
    background-color: #f3f3f3; }
    .site-header__category:hover {
      background-color: #e9e9e9; }
  .site-header__links {
    z-index: 0;
    position: absolute;
    padding: 0.25rem 0px;
    left: 50%;
    top: 100%;
    min-width: 100%;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    opacity: 0;
    transform: translate3d(-50%, -100%, 0px);
    transition: all 300ms ease; }
  .site-header__linkContainer:hover {
    background-color: #e3f2fd; }
  .site-header__link {
    display: block; }
  input:checked ~ .site-header__links {
    opacity: 1;
    transform: translate3d(-50%, 0px, 0px); } }

.site-footer {
  padding: 1rem;
  background-color: #f3f3f3; }
  .site-footer__githubContainer {
    display: block; }
  .site-footer__githubImage {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 32px; }

* {
  box-sizing: border-box; }

body {
  overflow-x: hidden; }

.content-area {
  margin-top: 1rem; }

.site-footer {
  margin-top: 1rem; }

body {
  display: flex; }

.site {
  display: flex;
  width: 100%;
  min-height: 100vh;
  flex-direction: column; }

.site-content {
  flex-grow: 1; }

.site-header, .site-footer {
  flex-shrink: 1; }

.container {
  padding-left: 1rem;
  padding-right: 1rem; }

.row {
  display: block;
  max-width: 800px;
  width: 100%;
  margin-left: auto;
  margin-right: auto; }
